//<1> el componente verga de react
import React, { Component } from 'react';
//<2> todos los de mas import de materialize
import AppBar from '@material-ui/core/AppBar';
import Toolbar from '@material-ui/core/Toolbar';
import MenuIcon from '@material-ui/core/Menu';
import IconButton from '@material-ui/core/IconButton';
//</2>
class Header extends Component{ //nuestro primer componente basado en clase
    render(){
        return(// tenemos que agregar la renderizacion de los componentes del appbar
            <AppBar
                position="fixed"
                style={{
                    backgroundColor:'#2f2f2f',
                    boxShadow:'none',
                    padding: '10px 0px'
                }}
                >
                <Toolbar>
                    <div className="header_logo">
                        <div className="header_logo_venue">
                            KYGO
                        </div>
                        <div className="header_logo_title">
                            Fell the Arcia
                        </div>
                    </div>    
                </Toolbar>    
            </AppBar>
        )
    }
}



export default Header
